import Filters from "../filtering/Filters";
import {url} from "../utils/utils";
import Swal from "@supportpal/sweetalert2";
import UnderlyingData from "../utils/UnderlyingData";

abstract class Card {
    filters: Filters;
    $elm: JQuery;
    opts: {} = {};

    constructor(filters: Filters, $elm: JQuery) {
        this.filters = filters;
        this.$elm = $elm;
    }

    abstract refresh(): JQueryPromise<any>;

    registerHandlers(): void {
        const instance = this;

        this.$elm
            .on('click', '.refresh-btn', () => instance.refresh())
            .on('click', '.export-csv-btn', function () {
                Swal.showLoading();

                const $item = $(this).parents('.grid-stack-item');
                const route = url($item.data('downloadCsvRoute'), instance.filters.all());
                $.get(route).done(() => Swal.fire(Lang.get('messages.success'), Lang.get('report.export_scheduled'), 'success'));
            })
            .on('click', '.print-btn', function () {
                const $item = $(this).parents('.grid-stack-item');
                const route = url($item.data('printRoute'), instance.filters.all());
                window.open(route, '_blank');
            })
            .on('click', '.underlying-btn', function () {
                const $item = $(this).parents('.grid-stack-item');
                UnderlyingData.show(
                    url($item.data('underlyingView'), instance.filters.all()),
                    url($item.data('underlyingExport'), instance.filters.all())
                );
            });
    }

    print(): void {
        this.refresh().then(() => setTimeout(() => window.print(), 250));
    }

    setOptions(opts: {}): this {
        this.opts = opts;

        return this;
    }

    underlyingDataRoute(): string {
        return this.$elm.data('underlyingView');
    }

    underlyingDataExportRoute(): string {
        return this.$elm.data('underlyingExport');
    }

    underlyingDataValueKey(): string {
        return this.$elm.data('underlyingValueKey');
    }

    implementsUnderlyingData(): boolean {
        return typeof this.underlyingDataRoute() !== 'undefined';
    }
}

export default Card;
